.multicolumn.padd_top {
  padding-top: 40px;
}
.multicolumn.grid {
  grid-template-columns: repeat(6, minmax(0, 1fr));
  list-style-type: none;
  margin: 40px 0 0 0;
  margin: 0;
  padding: 0;
}
.multicolumn {
  > .border {
    padding: 16px;
    border: 16px solid var(--color-green);
    @media screen and (min-width: 768px) {
      padding: 24px;
      border: 24px solid var(--color-green);
    }
  }
}
.column {
  position: relative;
  &.narrow {
    @media screen and (min-width: 1024px) {
      padding: 0 40px;
    }
    @media screen and (min-width: 1220px) {
      padding: 0 56px;
    }
  }
  .image {
    position: relative;
    z-index: 1;
    height: 100%;
    width: 100%;
    &.respect {
      height: auto;
      margin-bottom: 16px;
    }
    .buttons {
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 20;
      transform: translateX(-50%) translateY(-50%);
    }
    img {
      width: 100%;
      object-fit: cover;
      height: auto;
      display: block;
    }
    &.cover {
      img {
        height: 100%;
      }
    }
  }
  .button {
    margin-top: 16px;
  }

  h2, h3, h4, .h2, .h3, .h4, p, .buttons, small {
    flex: 1 1 100%;
  }
  .h3 {
    font-weight: 700;
  }
  h3.h2 {
    &.extrasmall {
      font-size: 100%;
      @media screen and (min-width: 768px) {
        font-size: 125%;
      }
      @media screen and (min-width: 1024px) {
        font-size: 150%;
      }
    }
    &.small {
      font-size: 125%;
      @media screen and (min-width: 768px) {
        font-size: 162.5%;
      }
      @media screen and (min-width: 1024px) {
        font-size: 200%;
      }
    }
    &.medium {
      font-size: 150%;
      @media screen and (min-width: 768px) {
        font-size: 200%;
      }
      @media screen and (min-width: 1024px) {
        font-size: 250%;
      }
    }
  }
  grid-column: span 6;
  @media screen and (min-width: 768px) {
    &.half {
      grid-column: span 3;
    }
    &.one_third {
      grid-column: span 2;
    }
    &.two_thirds {
      grid-column: span 4;
    }
  }
}