// Colours
.bg-green {
  background-color: var(--color-green);
}
.bg-pink {
  background-color: var(--color-red);
}
.bg-red {
  background-color: var(--color-pink);
}

.bg-darken, .bg-lighten {
  > div {
    z-index: 3;
    position: relative;
  }
  &:after {
    display: block;
    content: " ";
    position: absolute;
    inset: 0 16px;
    background-color: rgba(0,0,0,0.3);
    z-index: 1;
  }
}
.bg-lighten {
    &:after {
    background-color: rgba(255,255,255,0.3);
  }
}

.absolute {
  position: absolute;
  inset: 0;
}