.cart_close {
  background-color: rgba(255,255,255,0.0);
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  position: fixed;
  left: 0;
  top: 0;
  height: 0px;
}

.sidecart {
  position: fixed;
  display: none;
  opacity: 0;
  visibility: hidden;
  text-align: left;
  transition: all 0.3s cubic-bezier(0.390, 0.575, 0.565, 1.000);
  max-width: 375px;
  width: 320px;
  right: -320px;
  top: 56px;
  z-index: -1;
  background: transparent;
  color: var(--color-black);
  padding: 0;
  border-radius: 0;
  .button {
    width: 100%;
    margin-bottom: 8px;
  }
  form {
    padding: 16px;
    background: #FFFFFF;
    min-height: 100%;
    margin-left: 0;
    position: relative;
    div.h3 {
      margin: 24px 0 16px 0;
    }
    p.item_title {
      
    }
    p.intro {
      padding: 0 0 16px 0;
    }
    .cart_item {
      position: relative;
      margin-bottom: 12px;
      .cart_item_text {
        flex:1;
        font-size: 81.25%;
      }
      .qty {
        justify-content: flex-start;
        margin-bottom: 2px;
        label {
          flex: 0 1 auto;
        }
        input {
          flex: 0 1 auto;
          min-width: 20px;
          max-width: 30px;
          border-bottom: 0;
          background-color: #fff;
          padding: 1px 1px 1px 3px;
        }
      }
    }
  }
  @media screen and (min-width: 376px) {
    width: 375px;
    right: -375px;
  }
  &.alive {
    display: block;
    visibility: visible;
  }
  &.active {
  opacity: 1;
  z-index: 99999;
  right: 0;
  top: 56px;
  @media screen and (min-width: 415px) {
    right: 20px;
  }
  box-shadow: -38.2353px 0px 114.706px rgba(0, 0, 0, 0.2);
  &+.cart_close {
    height: 100vh;
    width: 100vw;
    z-index: 99998;
    cursor: default;
    }
  }
}
.sidecart .button.closecart {
  position: absolute;
  top: 8px;
  right: 4px;
  width: 40px;
  min-width: 30px;
}