.image-with-text {
  margin-top: 40px;
  img {
    object-fit: cover;
    display: block;
  }
  @media screen and (min-width: 880px) {
    gap: 32px;
  }
  .image {
    transition: var(--transition);
    grid-column: span 3;
    position: relative;
    @media screen and (min-width: 880px) {
      grid-column: 1 / 3;
      grid-row: 1/1;
    }
  }
  .text {
    height: 100%;
    .button {
      @media screen and (max-width: 480px) {
        width: 100%;
      }
    }
    .flex {
      height: 100%;
      align-items: center;
      align-content: center;
      justify-content: flex-start;
    }
    h2 {
      margin: 16px 0 8px;
      @media screen and (min-width: 768px) {
        margin: 0;
        margin-bottom: 8px;
      }
    }
    grid-column: span 3;
    @media screen and (min-width: 880px) {
      grid-column: 3 / 3;
      grid-row: 1/1;
    }
  }
  &.switch {
    .text {
      @media screen and (min-width: 880px) {
        grid-column: 1 / 2;
      }
    }
    .image {
      @media screen and (min-width: 880px) {
        grid-column: 2 / 4;
      }
    }
  }
}

.multicolumn{
  .text_wrap:not(.under) {
  position: absolute;
  top: 16px;
  left: 16px;
  right: 16px;
  bottom: 16px;
  z-index: 2;
  .hover & {
    opacity: 0;
    transition: var(--transition-slow);
  }
  @media screen and (min-width: 880px) {
    top: 32px;
    left: 32px;
    right: 32px;
    bottom: 32px;
  }
  .column.narrow & {
    @media screen and (min-width: 1220px) {
      left: 56px;
      right: 56px;
      }
    }
  }
  .hover & {
    .column:hover .text_wrap:not(.under) {
      opacity: 1;
    }
  }
}
.column.no-image .text_wrap:not(.under) {
  position: relative;
  //@media screen and (max-width: 879px) {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
  //}
}
.column.narrow.no-image .text_wrap:not(.under) {
  @media screen and (min-width: 768px) {
    left: 0;
    right: 0;
  }
}

@keyframes opacity_change {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.4;
  }
}

  
.multicolumn{  
  .column:not(.no-image) {
    .image {
      &:after {
        transition: var(--transition-slow);
        content: " ";
        background-color: transparent;
        opacity: 0;
        inset: 0;
        z-index: 3;
        display: block;
        position: absolute;
      }
    }
    &.bg-for-white_text {
      .image:after {
        background-color: var(--color-dark);
      }
    }
    &.bg-for-dark_text, &.bg-for-red_text, &.bg-for-blue_text {
      .image:after {
        background-color: white;
      }
    }
    .hover & {
      &:hover {
        .image {
          &:after {
            opacity: 0.4;
          }
        }
        &.bg-for-red_text, &.bg-for-blue_text {
          .image:after {
            opacity: 0.6;
          }
        }
      }
    }
    .nohoverboards & {
      .image {
        &:after {
        animation: opacity_change 8s linear infinite alternate both;
        }
      }
      &:nth-child(2) {
        .image {
          &:after {
            animation-delay: 1s;
          }
        }
      }
      &:nth-child(3) {
        .image {
          &:after {
            animation-delay: 3s;
          }
        }
      }
      &:nth-child(4) {
        .image {
          &:after {
            animation-delay: 5s;
          }
        }
      }
    }
  }
}

.text_wrap {
  z-index: 2;
  .h3 {
    font-weight: 700;
  }
  span {
    flex:  1 1 100%;
  }
}

