button.swatch {
  position: relative;
  overflow: hidden;
  width: 40px;
  height: 40px;
  background: transparent;
  margin: 0;
  border: 0;
}
button.swatch:hover {
  opacity: 0.9;
}
button.swatch:after {
  content: " ";
  display: block;
  background: black;
  width: 16px;
  height: 16px;
  position: absolute;
  inset: 12px;
  border-radius: 8px;
  border: 0px solid rgba(0, 0, 0, 0);
}
button.swatch.col-pink:after {
  border-color: var(--color-pink);
  background-color: var(--color-pink);
}
button.swatch.col-blue:after {
  border-color: var(--color-blue);
  background-color: var(--color-blue);
}
button.swatch.col-green:after {
  border-color: var(--color-green);
  background-color: var(--color-green);
}
button.swatch.col-red:after {
  border-color: var(--color-red);
  background-color: var(--color-red);
}
button.swatch.active:after {
  box-shadow: 0 2px 3px -1px rgba(0, 0, 0, 0.2);
}

